@mixin font-face($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;
    src: url("#{$font-path}/#{$file_name}.eot") format("eot"),
      url("#{$font-path}/#{$file_name}.woff") format("woff"),
      url("#{$font-path}/#{$file_name}.woff2") format("woff2"),
      url("#{$font-path}/#{$file_name}.ttf") format("truetype");
    font-weight: #{$weight};
    font-style: #{$style};
  }
}

@mixin flex-align-center {
